.list{
  background-color: #bf07ac;
  width: 300px;
  height: 50px;
  margin: 20px auto;
  font-size: 18px;
  border-radius: 5px;
  color: #fff;
}

.list p{
    padding: 10px;
    position: relative;
}

.list p span{
    position: absolute;
    right: 10px;
    cursor: pointer;

}
.list p input{
    background-color: transparent;
    border: 0;
    color: #fff;
    font-size: 18px;
}
.list p input:focus{
    outline: none;
}

.faicons{
    margin: 0 5px;
    font-size: 15px;
}